import {Component} from 'react'
import './index.css'
import UserProfile from '../TodoItem'

const initialTodosList = []

// Write your code here
console.log(initialTodosList)
class SimpleTodos extends Component {
  state = {
    usersDetailsList: initialTodosList,
  }

  deleteUser = id => {
    console.log('hi')
    console.log(id)
    const {usersDetailsList} = this.state
    const filteredUsersData = usersDetailsList.filter(each => each.id !== id)
    this.setState({
      usersDetailsList: filteredUsersData,
      text: '',
    })
  }

  changed = event => {
    this.setState({text: event.target.value})
    // console.log(event.target.value)
  }

  create = () => {
    const {text} = this.state
    const length1 = initialTodosList.length
    if (text !== undefined) {
      console.log(text)
      initialTodosList.push({id: length1 + 1, title: text})
      console.log(initialTodosList)
      this.setState({usersDetailsList: initialTodosList})
    }
  }

  render() {
    const {usersDetailsList} = this.state

    return (
      <div className="mainBackground">
        <div className="subBackground">
          <h1 className="headings">Your Personal Todos</h1>
          <div className="ins">
            <input
              className="inputed"
              placeholder="Input Todo"
              type="text"
              onChange={this.changed}
            />
            <button type="button" className="created" onClick={this.create}>
              Create
            </button>
          </div>
          <ul className="list-container">
            {usersDetailsList.map(eachUser => (
              <UserProfile
                userDetails={eachUser}
                key={eachUser.id}
                deleteUser={this.deleteUser}
              />
            ))}
          </ul>
        </div>
      </div>
    )
  }
}
export default SimpleTodos
